'use client';

import { useFormState, useFormStatus } from 'react-dom';
import { getCoffeeShopSuggestion, type SuggestionFormState } from '@/lib/actions';
import { Label } from '@/components/ui/label';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Wand2 } from 'lucide-react';
import { useEffect } from 'react';
import { useToast } from "@/hooks/use-toast";


interface SuggestionFormProps {
  onSuggestionReceived: (suggestion: string) => void;
}

function SubmitButton() {
  const { pending } = useFormStatus();
  return (
    <Button type="submit" disabled={pending} className="w-full">
      <Wand2 className="mr-2 h-4 w-4" />
      {pending ? 'Getting Suggestion...' : 'Get Suggestion'}
    </Button>
  );
}

export function SuggestionForm({ onSuggestionReceived }: SuggestionFormProps) {
  const initialState: SuggestionFormState = { message: null, errors: null, success: false };
  const [state, dispatch] = useFormState(getCoffeeShopSuggestion, initialState);
  const { toast } = useToast();

  useEffect(() => {
    if (state?.success && state.suggestion) {
      onSuggestionReceived(state.suggestion);
      toast({
        title: "AI Suggestion Ready!",
        description: `We found a suggestion for: ${state.suggestion}`,
      });
    } else if (!state?.success && state?.message && !state.errors) { // AI error, not validation
       toast({
        title: "Suggestion Error",
        description: state.message,
        variant: "destructive",
      });
    }
  }, [state, onSuggestionReceived, toast]);

  return (
    <Card className="shadow-lg">
      <CardHeader>
        <CardTitle className="font-headline text-xl">Find Your Perfect Coffee Spot</CardTitle>
        <CardDescription>Tell us your purpose (e.g., dating, working, studying) and let AI find a match!</CardDescription>
      </CardHeader>
      <form action={dispatch}>
        <CardContent className="space-y-4">
          <div>
            <Label htmlFor="purpose" className="font-medium">Your Purpose</Label>
            <Input
              id="purpose"
              name="purpose"
              type="text"
              placeholder="e.g., quiet place for dating"
              required
              className="mt-1"
            />
            {state?.errors?.purpose && (
              <p className="text-sm text-destructive mt-1">{state.errors.purpose.join(', ')}</p>
            )}
          </div>
        </CardContent>
        <CardFooter>
          <SubmitButton />
        </CardFooter>
      </form>
       {state?.message && !state.success && state.errors && (
         <p className="text-sm text-destructive p-4 pt-0">{state.message}</p>
       )}
    </Card>
  );
}
