import Image from 'next/image';
import type { CoffeeShop } from '@/types';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Star, MessageCircle } from 'lucide-react';

interface CoffeeShopCardProps {
  shop: CoffeeShop;
  onSelect?: (shop: CoffeeShop) => void;
}

export function CoffeeShopCard({ shop, onSelect }: CoffeeShopCardProps) {
  return (
    <Card 
      className="overflow-hidden shadow-lg hover:shadow-xl transition-shadow duration-300 cursor-pointer animate-card-reveal"
      onClick={() => onSelect?.(shop)}
      style={{ animationDelay: `${Math.random() * 0.3}s` }}
    >
      <CardHeader className="p-0">
        {shop.photos.length > 0 && (
          <div className="relative w-full h-48">
            <Image
              src={shop.photos[0]}
              alt={shop.name}
              layout="fill"
              objectFit="cover"
              data-ai-hint="coffee shop interior"
            />
          </div>
        )}
      </CardHeader>
      <CardContent className="p-4">
        <CardTitle className="text-xl font-headline mb-1">{shop.name}</CardTitle>
        <CardDescription className="text-sm text-muted-foreground mb-2">{shop.address}</CardDescription>
        <p className="text-sm mb-3 leading-relaxed">{shop.description.substring(0,100)}{shop.description.length > 100 ? '...' : ''}</p>
        <div className="flex items-center gap-2 mb-3">
          <Star className="w-4 h-4 text-yellow-500 fill-yellow-500" />
          <span className="font-medium">{shop.rating.toFixed(1)}</span>
          <MessageCircle className="w-4 h-4 text-gray-500 ml-2" />
          <span className="text-sm text-gray-500">{shop.reviews.length} reviews</span>
        </div>
        {shop.purposeKeywords && shop.purposeKeywords.length > 0 && (
          <div className="flex flex-wrap gap-2">
            {shop.purposeKeywords.map((keyword) => (
              <Badge key={keyword} variant="secondary" className="capitalize">
                {keyword}
              </Badge>
            ))}
          </div>
        )}
      </CardContent>
      {onSelect && (
        <CardFooter className="p-4 pt-0">
          {/* Could add a "View Details" button or similar action here */}
        </CardFooter>
      )}
    </Card>
  );
}
