'use client';

import { APIProvider, Map, AdvancedMarker, Pin, InfoWindow } from '@vis.gl/react-google-maps';
import type { CoffeeShop } from '@/types';
import { useState, useEffect } from 'react';

interface CoffeeMapProps {
  shops: CoffeeShop[];
  selectedShop?: CoffeeShop | null;
  onMarkerClick?: (shop: CoffeeShop) => void;
  center?: { lat: number; lng: number };
  zoom?: number;
}

const MAP_ID = "COFFEE_COMPASS_MAP";

export function CoffeeMap({ shops, selectedShop, onMarkerClick, center, zoom = 13 }: CoffeeMapProps) {
  const apiKey = process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY;
  const [currentCenter, setCurrentCenter] = useState(center || { lat: 34.052235, lng: -118.243683 }); // Default to LA
  const [infoWindowShop, setInfoWindowShop] = useState<CoffeeShop | null>(null);

  useEffect(() => {
    if (selectedShop) {
      setCurrentCenter({ lat: selectedShop.lat, lng: selectedShop.lng });
      setInfoWindowShop(selectedShop);
    } else if (shops.length > 0 && !center) {
      // If no specific center or selected shop, center on the first shop in the list
      setCurrentCenter({ lat: shops[0].lat, lng: shops[0].lng });
    }
  }, [selectedShop, shops, center]);


  if (!apiKey) {
    return (
      <div className="flex items-center justify-center h-full bg-muted rounded-lg p-4">
        <p className="text-destructive text-center">
          Google Maps API key is missing. Please add NEXT_PUBLIC_GOOGLE_MAPS_API_KEY to your .env.local file.
        </p>
      </div>
    );
  }
  
  const handleMarkerClickInternal = (shop: CoffeeShop) => {
    setInfoWindowShop(shop);
    if (onMarkerClick) {
      onMarkerClick(shop);
    }
  };

  return (
    <APIProvider apiKey={apiKey}>
      <div className="h-full w-full rounded-lg overflow-hidden shadow-md">
        <Map
          defaultCenter={currentCenter}
          center={currentCenter}
          defaultZoom={zoom}
          zoom={zoom}
          gestureHandling={'greedy'}
          disableDefaultUI={true}
          mapId={MAP_ID}
          className="min-h-[300px] md:min-h-0"
        >
          {shops.map((shop) => (
            <AdvancedMarker
              key={shop.id}
              position={{ lat: shop.lat, lng: shop.lng }}
              onClick={() => handleMarkerClickInternal(shop)}
              title={shop.name}
            >
              <Pin 
                background={selectedShop?.id === shop.id ? 'var(--color-primary)' : 'var(--color-accent)'}
                borderColor={selectedShop?.id === shop.id ? 'var(--color-primary-foreground)' : 'var(--color-accent-foreground)'}
                glyphColor={selectedShop?.id === shop.id ? 'var(--color-primary-foreground)' : 'var(--color-accent-foreground)'}
              />
            </AdvancedMarker>
          ))}

          {infoWindowShop && (
            <InfoWindow
              position={{ lat: infoWindowShop.lat, lng: infoWindowShop.lng }}
              onCloseClick={() => setInfoWindowShop(null)}
              pixelOffset={[0, -30]}
            >
              <div className="p-2 max-w-xs">
                <h3 className="font-bold text-md mb-1">{infoWindowShop.name}</h3>
                <p className="text-xs text-muted-foreground">{infoWindowShop.address}</p>
              </div>
            </InfoWindow>
          )}
        </Map>
      </div>
    </APIProvider>
  );
}
