'use client';

import { useState, useEffect, useCallback } from 'react';
import type { CoffeeShop } from '@/types';
import { allCoffeeShops as initialShops } from '@/data/mock-coffee-shops';
import { CoffeeMap } from '@/components/coffee-map';
import { CoffeeShopList } from '@/components/coffee-shop-list';
import { SuggestionForm } from '@/components/suggestion-form';
import { SearchBar } from '@/components/search-bar';
import { Header } from '@/components/header';
import { Separator } from '@/components/ui/separator';
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { Terminal } from "lucide-react";

export default function HomePage() {
  const [displayedShops, setDisplayedShops] = useState<CoffeeShop[]>(initialShops);
  const [selectedShop, setSelectedShop] = useState<CoffeeShop | null>(null);
  const [mapCenter, setMapCenter] = useState<{ lat: number; lng: number } | undefined>(undefined);
  const [aiSuggestedShopName, setAiSuggestedShopName] = useState<string | null>(null);

  const handleSearch = useCallback((searchTerm: string) => {
    setAiSuggestedShopName(null); // Clear AI suggestion when searching manually
    if (!searchTerm.trim()) {
      setDisplayedShops(initialShops);
      setSelectedShop(null);
      if (initialShops.length > 0) {
        setMapCenter({ lat: initialShops[0].lat, lng: initialShops[0].lng });
      }
      return;
    }
    const filtered = initialShops.filter(shop =>
      shop.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      shop.address.toLowerCase().includes(searchTerm.toLowerCase()) ||
      (shop.purposeKeywords && shop.purposeKeywords.some(keyword => keyword.toLowerCase().includes(searchTerm.toLowerCase())))
    );
    setDisplayedShops(filtered);
    setSelectedShop(null);
    if (filtered.length > 0) {
      setMapCenter({ lat: filtered[0].lat, lng: filtered[0].lng });
    }
  }, []);

  const handleSuggestionReceived = useCallback((suggestedShopName: string) => {
    setAiSuggestedShopName(suggestedShopName);
    const foundShop = initialShops.find(
      shop => shop.name.toLowerCase() === suggestedShopName.toLowerCase()
    );

    if (foundShop) {
      setDisplayedShops([foundShop]);
      setSelectedShop(foundShop);
      setMapCenter({ lat: foundShop.lat, lng: foundShop.lng });
    } else {
      // If AI suggests a shop not in our mock data, still show the AI text, clear list
      setDisplayedShops([]);
      setSelectedShop(null);
      // Optionally, you could try to geocode the suggestedShopName if it contains an address
      // For now, we just clear the map center or keep it as is.
    }
  }, []);
  
  const handleShopSelectFromList = (shop: CoffeeShop) => {
    setSelectedShop(shop);
    setMapCenter({ lat: shop.lat, lng: shop.lng });
  };

  useEffect(() => {
    // Set initial map center based on the first shop if available
    if (initialShops.length > 0 && !mapCenter) {
      setMapCenter({ lat: initialShops[0].lat, lng: initialShops[0].lng });
    }
  }, [mapCenter]);


  return (
    <div className="flex flex-col min-h-screen bg-background">
      <Header />
      <main className="flex-grow container mx-auto px-4 py-8">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          {/* Left Column: Controls and List */}
          <div className="md:col-span-1 space-y-6 flex flex-col">
            <SearchBar onSearch={handleSearch} />
            <SuggestionForm onSuggestionReceived={handleSuggestionReceived} />
            
            {aiSuggestedShopName && (
              <Alert variant={displayedShops.length > 0 && displayedShops[0].name.toLowerCase() === aiSuggestedShopName.toLowerCase() ? "default" : "destructive"} className="mt-4">
                <Terminal className="h-4 w-4" />
                <AlertTitle className="font-headline">AI Suggestion</AlertTitle>
                <AlertDescription>
                  {displayedShops.length > 0 && displayedShops[0].name.toLowerCase() === aiSuggestedShopName.toLowerCase()
                    ? `Found: "${aiSuggestedShopName}"`
                    : `AI suggested "${aiSuggestedShopName}", but it's not in our current listings. Try a broader search or a different purpose.`}
                </AlertDescription>
              </Alert>
            )}
            
            <Separator />
            <div className="flex-grow overflow-y-auto max-h-[calc(100vh-400px)] md:max-h-none pr-2"> {/* Added pr-2 for scrollbar space */}
               <h2 className="text-2xl font-headline font-semibold mb-4 text-primary">
                {aiSuggestedShopName && displayedShops.length === 1 ? 'AI Recommended' : 'Coffee Shops'}
              </h2>
              <CoffeeShopList shops={displayedShops} onShopSelect={handleShopSelectFromList} />
            </div>
          </div>

          {/* Right Column: Map */}
          <div className="md:col-span-2 h-[400px] md:h-auto md:min-h-[600px] rounded-lg shadow-xl">
             <CoffeeMap shops={displayedShops} selectedShop={selectedShop} onMarkerClick={handleShopSelectFromList} center={mapCenter} />
          </div>
        </div>
      </main>
      <footer className="text-center py-6 text-sm text-muted-foreground">
        <p>&copy; {new Date().getFullYear()} Coffee Compass. All rights reserved.</p>
      </footer>
    </div>
  );
}
