import type { CoffeeShop } from '@/types';
import { CoffeeShopCard } from './coffee-shop-card';

interface CoffeeShopListProps {
  shops: CoffeeShop[];
  onShopSelect?: (shop: CoffeeShop) => void;
}

export function CoffeeShopList({ shops, onShopSelect }: CoffeeShopListProps) {
  if (shops.length === 0) {
    return <p className="text-center text-muted-foreground py-8">No coffee shops found. Try adjusting your search or suggestion.</p>;
  }

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 p-4">
      {shops.map((shop) => (
        <CoffeeShopCard key={shop.id} shop={shop} onSelect={onShopSelect} />
      ))}
    </div>
  );
}
